import { LitElement, css, html, unsafeCSS } from "lit";
import { customElement, property } from "lit/decorators.js";
import { LinkObject } from "../types/LinkObject";
// import styles from "../styles/dfci-header.scss";

@customElement("dfci-header")
export class DfciHeader extends LitElement {
    @property() name = "Test Link";
    @property({ type: Array }) links: LinkObject[] = [];

    // static styles = unsafeCSS(styles);

    render() {
        return html`
            <header>
                <a class="logo" href="/">
                    <img
                        src="/assets/dfci-logo.png"
                        alt="Dana-Farber Cancer Institute"
                    />
                </a>
                <slot name="links"></slot>
                <div class="extra">
                    <input name="search-bar" type="text" placeholder="Search" />
                </div>
            </header>
        `;
    }
}
