import { LitElement, html } from "lit";
import { customElement, property } from "lit/decorators.js";

@customElement("dfci-page-header")
export class DfciPageHeader extends LitElement {
    @property({ type: String, attribute: "image-src" }) imageSrc?: string;
    @property({ type: String, attribute: "image-alt" }) imageAlt?: string;

    // static styles = unsafeCSS(styles);

    render() {
        return html`
            <figure>
                <img src=${this.imageSrc} alt=${this.imageAlt} />
            </figure>
            <h1 class="heading">
                <slot></slot>
            </h1>
        `;
    }
}

declare global {
    interface HTMLElementTagNameMap {
        "dfci-page-header": DfciPageHeader;
    }
}
