import { LitElement, css, html } from "lit";
import { customElement, property } from "lit/decorators.js";

type ImageOrientation = "portrait" | "landscape" | "square";

@customElement("dfci-image")
export class DfciImage extends LitElement {
    @property() orientation: ImageOrientation = "square";
    @property() src?: string;
    @property() alt?: string;

    static styles = css`
        :host figure {
            --image-aspect-ratio: 1/1;
        }

        :host([orientation="square"]) figure {
            --image-aspect-ratio: 1/1;
        }

        :host([orientation="portrait"]) figure {
            --image-aspect-ratio: 423 / 566;
        }

        :host([orientation="landscape"]) figure {
            --image-aspect-ratio: 384 / 216;
        }

        figure {
            margin: unset;
            padding: unset;
            display: flex;
            width: 100%;

            aspect-ratio: var(--image-aspect-ratio, 1/1);
        }

        figure img {
            display: flex;
            object-fit: cover;
            background-color: lightgrey;
            width: 100%;
            height: 100%;
        }
    `;

    render() {
        return html`
            <figure>
                <img src=${this.src} alt=${this.alt} />
            </figure>
        `;
    }
}
